.header{
  position: absolute;
  left: 0;
  top: 45px;
  width: 100%;
  &__container{
    padding: 0 30px;

    width: 100%;
    max-width: 1410px;

    margin: 0 auto;
  }
  &__burger{
    display: none;
  }
  &__row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-block{
      display: flex;
      align-items: center;
    }
  }
  &__logo{
    height: 96px;
  }
  &__nav{
    margin-left: 96px;
  }
  &__phone{
    margin-left: 110px;
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
    &__item{
      & + &{
        margin-left: 45px;
      }
    }
    &__link{
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 21px;

      color:#F8FAFE;
    }
  }
}

.logo{
  height: 100%;
  width: auto;
  &__img{
    width: 100%;
    height: 100%;

    object-fit: contain;
  }
}

.phone{
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 600;
  font-size: 26px;
  color: #F8FAFE;
}

.language{
  &-list{
    display: flex;
    align-items: center;
    &__item{
      position: relative;

      color:#BCC2CE;
      &::before{
        content: '';
        position: absolute;
        left: calc(50% - 4px);
        bottom: -16px;

        display: none;
        width: 8px;
        height: 8px;

        border-radius: 50%;

        background: #F8FAFE;
      }
      &.active{
        color:#fff;
        &::before{
          display: block;
        }
      }
      & + &{
        margin-left: 20px;
      }
    }
    &__link{
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 21px;
      color:inherit;

      text-transform: uppercase;
    }
  }
}

.burger{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  width: 40px;
  height: 11px;

  &__item{
    flex-shrink: 0;

    width: 100%;
    height: 1px;

    background: #F8FAFE;
  }
}

@media screen and (max-width: 1200px) {
  .header{
    &__phone,&__nav{
      margin-left: 30px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .header{
    top: 30px;
    &__logo{
      height: 60px;
    }
    &__burger{
      display: block;
    }
    &__phone,&__language{
      display: none;
    }
    &__nav{
      display: none;
    }
    &-nav{
      &__list{
        align-items: flex-end;
        flex-direction: column;
      }
      &__item{
        & + &{
          margin-left: 0;

          margin-top: 10px;
        }
      }
    }
  }
}