body{
  background-color: #000;

  color:#fff;
}


.container{
  width: 100%;
  max-width: 1500px;

  padding: 0 30px;

  margin: 0 auto;
}

.button{
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 80px;

  &-transparent{
    border: 1px solid rgba(248, 250, 254, 0.5);
    border-radius: 10px;

    font-family: 'IBM Plex Mono', monospace;
    font-weight: 500;
    font-size: 20px;
    color: #F8FAFE;
  }
  &-purple{
    background: #6640FF;
    border-radius: 10px;

    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 20px;
    color: #F8FAFE;
  }
  &-big{
    max-width: 474px;
  }

  &-center{
    margin: 0 auto;
  }
}

.home-page{
  &__wrapper{
    position: relative;
    overflow: hidden;
    &::before{
      content: '';
      position: absolute;
      right: 0;
      top: -200px;

      width: 880px;
      height: 860px;
      display: block;

      animation: fragment backwards 10s ease-in-out infinite;

      background: url("../img/hero-coub.png") center center/contain no-repeat;
    }
  }
  &__services{
    margin-top: 135px;
  }
  &__projects{
    margin-top: 170px;
  }
  &__technology{
    margin-top: 125px;
  }
  &__development{
    margin-top: 135px;
  }
  &__banner{
    margin-top: 160px;
  }
  &__works{
    margin-top: 130px;
  }
}

@media screen and (max-width: 767px) {
  .button{
    height: 60px;
    font-size: 16px;
    &-purple{
      font-size: 16px;
    }
  }
  .home-page{
    &__wrapper{
      &::before{
        width: 550px;
        height: 550px;

        top: -150px;
        right: -100px;
      }
    }
    &__services{
      margin-top: 70px;
    }
    &__projects{
      margin-top: 55px;
    }
    &__technology{
      margin-top: 94px;
    }
    &__works{
      margin-top: 80px;
    }
    &__development{
      margin-top: 60px;
    }
    &__banner{
      margin-top: 85px;
    }
  }
}